.el-CapForm__formHeader:after {
            display: inline-block;
            content: "";
            height: 100%;
            vertical-align: middle;
}
.el-CapForm .el-form {}
.el-CapForm .el-form-item {
            margin-bottom: 0;
}
.el-CapForm table, .el-CapForm td, .el-CapForm th, .el-CapForm tr {
            box-sizing: border-box;
            margin: 0;
            padding: 0;
}
.el-CapForm table {
            border-spacing: 0px;
            table-layout: fixed
}
.el-CapForm table + table {
            margin-top: 20px;
}
.el-CapForm td {
            border: 1px solid #B6B6B6;
            border-bottom: 0;
            overflow-x: hidden;
            padding: 0
}
.el-CapForm td > .el-form-item {}
.el-CapForm td ~ td {
            border-left: 0;
}
.el-CapForm table tr:last-child td {
            border-bottom: 1px solid #B6B6B6;
}
.el-CapForm__header {
            padding: 10px 20px;
            border-bottom: 1px solid #CECECE;
}
.el-CapForm__formHeader {
            background: #3AADFB;
            text-align: center;
            color: #fff;
            font-size: 24px;
            height: 80px;
}



