.el-Sheet__formHeader:after {
            display: inline-block;
            content: "";
            height: 100%;
            vertical-align: middle;
}
.el-Sheet .el-form {}
.el-Sheet .el-form-item {
            margin-bottom: 0;
}
.el-Sheet table, .el-Sheet td, .el-Sheet th, .el-Sheet tr {
            box-sizing: border-box;
            margin: 0;
            padding: 0;
}
.el-Sheet table {
            border-spacing: 0px;
            table-layout: fixed
}
.el-Sheet table + table {
            margin-top: 20px;
}
.el-Sheet td {
            border: 1px solid #B6B6B6;
            border-bottom: 0;
            overflow-x: hidden;
            padding: 0
}
.el-Sheet td > .el-form-item {}
.el-Sheet td ~ td {
            border-left: 0;
}
.el-Sheet table tr:last-child td {
            border-bottom: 1px solid #B6B6B6;
}
.el-Sheet__header {
            padding: 10px 20px;
            border-bottom: 1px solid #CECECE;
}
.el-Sheet__content {
            margin: 46px auto 0;
}
.el-Sheet__formHeader {
            background: #3AADFB;
            text-align: center;
            color: #fff;
            font-size: 24px;
            height: 80px;
}



